import { HomeCrumb } from "@/components/bread-crumb";
import { Container } from "@/components/container";
import { InfoHeader } from "@/components/div";
import { Section } from "@/components/section";
import { InfoTitle } from "@/components/title";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import Link from "next/link";

const DeliveryPage = () => {
  return (
    <Container className="pb-[58px]">
      <HomeCrumb paths={[{ name: "Доставка товара" }]} />
      <Section className="py-6 px-0">
        <InfoHeader className="mb-5">
          <InfoTitle>Доставка товара</InfoTitle>
        </InfoHeader>
        <div className="p-6">
          <h4 className="info-text mb-5">
            Для Вашего удобства мы предлагаем несколько способов доставки на выбор.
          </h4>
          <h2 className="info-title mb-5">Самовывоз</h2>
          <p className="info-text">
            Вы можете забрать груз самостоятельно или доверить это курьерской компании по адресу самовывоза.
          </p>

          {/* ✅ Table for pickup points */}
          <Table className="mt-6 border border-gray-300 rounded-lg overflow-hidden">
            <TableHeader>
              <TableRow className="bg-gray-100 text-left">
                <TableHead className="w-1/4 px-4 py-3">Город</TableHead>
                <TableHead className="w-1/2 px-4 py-3">Адрес пунктов самовывоза</TableHead>
                <TableHead className="w-1/4 px-4 py-3">Время работы</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              <TableRow>
                <TableCell className="font-semibold text-orange-500 px-4 py-3">Ташкент</TableCell>
                <TableCell className="px-4 py-3">
                  <p>Мирзо-Улугбекский р-н, ул. Сайрам 7-тор (бывш. Э.Мараимова), д.52</p>
                  <Link 
                    href="tel:+998555080660"
                    className="text-blue-500 hover:underline"
                  >
                    +998 55 508 0660 (доб. 924)
                  </Link>
                </TableCell>
                <TableCell className="px-4 py-3">ПН-ПТ <br /> с 9:00 до 18:00 часов</TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </div>
      </Section>
    </Container>
  );
};

export default DeliveryPage;
